body{
  background-color: antiquewhite;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.App-title{
  text-align: center;
}

.inputfield{
  margin-right: 20% !important;
}
.center{
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;

}
.card{
  padding: 15% !important;
}
.carddetails{
  display: 'flex';
  flex-direction: 'column';
}
.content{
  flex: '1 0 auto';
}
.cover{
  min-width:'25%' !important;
  max-width:'25%' !important;
}
.innercard{
  margin-bottom: 2%;
}